html {
  overflow-y: hidden;
}

.edit-area {
  width: 100%;
}

.app-header {
  flex-grow: 0;
  height: 9vh;
}

.header-icon {
  vertical-align: middle;
  margin-left: 0.5em;
}

.header-left {
  padding: 0;
  flex: 1;
}

.monster-area {
  display: flex;
  flex-wrap: wrap;

  flex-grow: 1;
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: gray;

  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: dimgray;
}

.contain {
  flex-grow: 1;
  overflow-y: scroll;
  overflow-x: hidden;
  height: 70vh;
  background-color: whitesmoke;
}

.monster {
  margin-top: 1em;
  margin-bottom: 1em;
  margin-left: 0.5em;
  margin-right: 0.5em;
  width: 20em;
  height: fit-content;
}

.icon {
  width: 1em;
  height: 1em;
}

.search {
  flex-grow: 0;
  min-width: 20em;
}

.save {
  flex: 1;
}

select {
  height: 3.5em;
  width: 15em;
}
